.footer {
  background-color: #008aff;
  border-radius: 0 200px 0 0;
  padding: 180px 0 84px;
  color: white;

  &__information {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 226px;
    margin-bottom: 121px;
  }

  &__nav-wrapper {
    display: flex;
    justify-content: space-between;
  }
}
